<template>
  <div class="loginForm">
    <p class="text-center h1 p-5"></p>
    <b-form-group id="input-group-1" label-for="input-1">
      <b-form-input
        class="form-control form-control-lg"
        id="input-1"
        v-model="user.email"
        type="email"
        ref="email"
        placeholder=""
        required
      ></b-form-input>
      <p class="text-danger" v-show="valueCheck.email">
         .
      </p>
    </b-form-group>
    <b-form-group id="input-group-2" label-for="input-2">
      <b-form-input
        class="form-control form-control-lg"
        id="input-2"
        v-model="user.password"
        type="password"
        ref="password"
        placeholder=""
        @keyup.enter="onLogin"
        required
      ></b-form-input>
      <p class="text-danger" v-show="showLoginFailMsg">
          ,  .
      </p>
      <p class="text-danger" v-show="valueCheck.password">
         .
      </p>
    </b-form-group>
    <b-button
      type="submit"
      class="btn-fill"
      variant="primary"
      size="lg"
      block
      @click="onLogin"
      ><span></span></b-button
    >
    <div class="py-3">
      <b-link class="pr-3" href="#" @click="onclickRegister"></b-link>
      <b-link class="pr-3" href="#" @click="openPasswordFindModal"
        > </b-link
      >
      <b-link href="#" id="test-login" @click="onTestLogin"> </b-link>
      <b-tooltip target="test-login" triggers="hover" variant="dark">
            .
      </b-tooltip>
    </div>
    <!--   Modal -->
    <passwordFind v-if="showPasswordFindModal">
      <!-- top   -->
      <template slot="top">
        <span> </span>
      </template>
      <!-- /top -->
      <!-- cotent   -->
      <div class="text-center" v-show="modalContent1">
        <input
          type="email"
          ref="emailModal"
          v-model="email"
          placeholder=" ."
          @keyup.enter="onclickSearch"
        />
        <button class="primaryBtn ml-1" @click="onclickSearch"></button>
      </div>
      <div class="text-center" v-show="modalContent2">
        <input type="radio" checked="checked" />&ensp;<span
          >{{ email }}()  </span
        >
        <button class="primaryBtn ml-3" @click="sendPasswordResetEmail">
          
        </button>
      </div>
      <div class="text-center" v-show="modalContent3">
        <span
          >{{ email }}()   .<br />
                .</span
        >
      </div>
      <!-- /cotent -->
      <!-- footer   -->
      <template slot="footer">
        <button class="basicBtn" @click="closePasswordFindModal"></button>
      </template>
      <!-- /footer -->
    </passwordFind>
  </div>
</template>

<script>
import passwordFind from "src/components/Modal/PasswordFind";
export default {
  name: "Login",
  components: { passwordFind },
  data() {
    return {
      user: {
        email: "",
        password: "",
      },
      email: "",
      valueCheck: {
        email: false,
        password: false,
      },
      showLoginFailMsg: false,
      showPasswordFindModal: false,
      modalContent1: true,
      modalContent2: false,
      modalContent3: false,
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.userStore.initialState.status.loggedIn;
    },
  },
  mounted() {
    if (this.loggedIn) {
      this.$router.push("/");
    }
  },
  methods: {
    validationCheck() {
      this.showLoginFailMsg = false;
      this.valueCheck.email = false;
      this.valueCheck.password = false;
      if (!this.user.email) {
        this.valueCheck.email = true;
        this.$refs.email.focus();
      } else if (!this.user.password) {
        this.valueCheck.password = true;
        this.$refs.password.focus();
      }
    },
    onLogin() {
      this.validationCheck();
      if (this.user.email && this.user.password) {
        this.$store.dispatch("userStore/login", this.user).then(
          (data) => {
            if (data.error) {
              //  
              this.user.password = "";
              this.$refs.password.focus();
              this.showLoginFailMsg = true;
            } else {
              //  
              this.$router.push("/");
            }
          },
          (error) => {}
        );
      }
    },
    //  click
    onclickRegister() {
      this.$router.push("/user/register");
    },
    //    open
    openPasswordFindModal() {
      this.showPasswordFindModal = true;
      this.modalContent1 = true;
      this.modalContent2 = false;
      this.modalContent3 = false;
      this.email = "";
    },
    //    close
    closePasswordFindModal() {
      this.showPasswordFindModal = false;
      this.modalContent1 = true;
      this.modalContent2 = false;
      this.modalContent3 = false;
      this.email = "";
    },
    //      click
    onclickSearch() {
      if (this.email) {
        //   
        this.$store
          .dispatch("userStore/selectRegisterCheck", this.email)
          .then((res) => {
            if (res.email) {
              this.modalContent1 = false;
              this.modalContent2 = true;
              this.modalContent3 = false;
            } else {
              alert("    .");
              this.$refs.emailModal.focus();
            }
          })
          .catch((Error) => {
            console.log(Error);
          });
      } else {
        alert("  .");
      }
    },
    //    
    sendPasswordResetEmail() {
      if (this.email === "test@test.com") {
        alert("    .");
      } else {
        this.$store
          .dispatch("userStore/sendPasswordResetEmail", this.email)
          .then(() => {
            this.modalContent1 = false;
            this.modalContent2 = false;
            this.modalContent3 = true;
          })
          .catch((Error) => {
            console.log(Error);
          });
      }
    },
    //   
    onTestLogin() {
      this.$store
        .dispatch("userStore/testIdLogin")
        .then(() => {
          //   
          this.$router.push("/");
        })
        .catch((Error) => {
          console.log(Error);
        });
    },
  },
};
</script>
