.loginForm {
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  width: 30%;
  min-height: calc(100% - 60px);
  /* height: inherit; */
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-align: center;
}
li {
  display: inline;
}
li a {
  padding: 20px;
}
</style>